// hook
import { ref, onMounted, reactive } from "vue";
import { reqNewAlbum } from "@/api";

export default function new_album() {
  // 
  let newAlbums = ref([])
  // 
  const getNewAlbum = async () => {
    const result = await reqNewAlbum()
    if (result.code === 200) {
      newAlbums.value = result.albums
    }
  }
  onMounted(() => {
    getNewAlbum()
  })
  return {
    newAlbums,
    getNewAlbum,
  }
}
